import React from 'react';
import {Icon} from "../asset/js/icon";

const Modal = (props) => {
	// , ,     
	const { open, close, header, submit, hasFooter } = props;

	const onDimmedClick = (e) => {
		if(typeof e.target.className === 'string' && e.target.className.indexOf('openModal') === 0) {
			close();
		}
	}

	return (
		//   openModal  .
		<>
			{open ? (
				<div className={open ? 'openModal modal' : 'modal'} onClick={e => onDimmedClick(e)}>
					<section className={'modal_section'}>
						<header className={'modal_header'}>
							{header}
							<button className={'btn_close'} onClick={close}><i><Icon.ic24Close/></i></button>
						</header>
						<main className={'main'}>{props.children}</main>
						{hasFooter !== false &&
							<footer className={'modal_footer'}>
								<button type={'button'} className={'btn_submit'} onClick={close}>{submit ? submit : ''}</button>
							</footer>
						}

					</section>
				</div>
			) : null}
		</>
	);
};

export default Modal;